if (!runningTests) {
  require("dummy/app")["default"].create({"name":"ember-power-select","version":"0.10.0-beta.8+c90b8b1a"});
}

define('~fastboot/app-factory', ['dummy/app', 'dummy/config/environment'], function(App, config) {
  App = App['default'];
  config = config['default'];

  return {
    'default': function() {
      return App.create(config.APP);
    }
  };
});


