        dom.appendChild(el1, el2);
        var el2 = dom.createTextNode("{{levelOrOption.levelName}} ></strong>\n    ");
        dom.appendChild(el1, el2);
        var el2 = dom.createTextNode("{{else}}\n      ");
        dom.appendChild(el1, el2);
